.btn {
  padding: 5px;
  border-radius: 10px;
  color: white;
  border: none;
  cursor: pointer;
}

.btn-primary {
  background-color: rgb(85, 88, 237);
}

.btn-secondary {
  background-color: rgb(88, 82, 84);
}

.btn-small {
  font-size: 14px;
}

.btn-medium {
  font-size: 16px;
  width: 150px;
}

.btn-disabled {
  background-color: rgb(88, 82, 84);
}
